import { FC } from "react";
import Slider from "react-slick";
import useWindowSize, { isDesktop } from "../../../hooks/useWindowSize";
import { Container } from "../Container/Container";
import { StyledTestimonialItem, TestimonialText, TestimonialAuthor, TestimonialsWrapper, TestimonialsContainer, TestimonialItems } from "./styled";
import React from "react";
import QuoteIcon from "../Icons/QuoteIcon";

type TestimonialCommonProps = {
    quoteIcon?: boolean;
    authorPositionLabelPlacement: 'row' | 'column';
    forceArrows?: boolean;
    forceInfinite?: boolean;
}

type TestimonialItemType = {
    text: string;
    authorName: string;
    authorPosition: string;
};

type TestimonialItemProps = TestimonialCommonProps & {
    testimonial: TestimonialItemType;
}

const TestimonialItem: FC<TestimonialItemProps> = ({
    testimonial,
    quoteIcon,
    authorPositionLabelPlacement
}) => {
    return (
        <StyledTestimonialItem>
            {quoteIcon && <QuoteIcon />}
            <TestimonialText>{testimonial.text}</TestimonialText>
            <TestimonialAuthor authorPositionLabelPlacement={authorPositionLabelPlacement}>
                <span>{testimonial.authorName}</span>{authorPositionLabelPlacement === "row" && ", "}<span>{testimonial.authorPosition}</span>
            </TestimonialAuthor>
        </StyledTestimonialItem>
    );
};

type TestimonialsProps = TestimonialCommonProps & {
    testimonials: TestimonialItemType[];
};

export const Testimonials: FC<TestimonialsProps> = ({ testimonials, authorPositionLabelPlacement, quoteIcon, forceArrows, forceInfinite }) => {
    const windowSize = useWindowSize();
    const isDesktopScreen = isDesktop(windowSize.width as number);

    const settings = {
        infinite: forceInfinite ?? !isDesktopScreen,
        speed: 500,
        slidesToShow: isDesktopScreen ? 3 : 1,
        slidesToScroll: 1,
        autoplay: false,
        arrows: forceArrows ?? !isDesktopScreen,
    };

    return (
        <TestimonialsWrapper>
            <Container>
                <TestimonialsContainer>
                    <TestimonialItems>
                        <Slider {...settings}>
                            {testimonials.map(testimonial => <TestimonialItem
                                key={testimonial.text}
                                testimonial={testimonial}
                                authorPositionLabelPlacement={authorPositionLabelPlacement}
                                quoteIcon={quoteIcon}
                            />)}
                        </Slider>
                    </TestimonialItems>
                </TestimonialsContainer>
            </Container>
        </TestimonialsWrapper>
    );
};
