import Slider from 'react-slick';
import useWindowSize, { isDesktop } from '../../hooks/useWindowSize';
import React from 'react';
import { VideoCard } from '../common/VideoCard';
import { StaticImage } from 'gatsby-plugin-image';
import { VideoCardContainer, VideosListContainer, VideosListSlide } from './styled';

export const VideosList = () => {
  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  const settings = {
    infinite: !isDesktopScreen,
    speed: 500,
    slidesToShow: isDesktopScreen ? 2 : 1,
    slidesToScroll: 1,
    autoplay: false,
    arrows: !isDesktopScreen,
  };

  return (
    <VideosListContainer>
      <Slider {...settings}>
        <VideosListSlide>
          <VideoCardContainer>
            <VideoCard
              videEmbeddedLink="https://www.youtube.com/embed/IePJyN8x30g?si=Ai2x0pqgSbTuErrK"
              videoExternalLink="https://youtu.be/IePJyN8x30g"
              linkTitle="Watch the testimonial"
            >
              <StaticImage
                src="../../images/CustomerStories/VideoPreviews/OnlySalesTestimonial.png"
                alt="OnlySales testimonial"
              ></StaticImage>
            </VideoCard>
          </VideoCardContainer>
        </VideosListSlide>
        <VideosListSlide>
          <VideoCardContainer>
            <VideoCard
              videEmbeddedLink="https://www.youtube.com/embed/-Q7waqXAF5s?si=kh_KqgOgrDYQeKEW"
              videoExternalLink="https://youtu.be/81VxdPrZJOU"
              linkTitle="Watch the webinar"
            >
              <StaticImage
                src="../../images/CustomerStories/VideoPreviews/WallaWebinarPreview.webp"
                alt="Webinar preview"
              ></StaticImage>
            </VideoCard>
          </VideoCardContainer>
        </VideosListSlide>
      </Slider>
    </VideosListContainer>
  );
};
